import React from "react";

import Landing from "../components/Landing";
import Logo from "../components/Logo";

import details from "../data/details.json";

const Home = () => {
  return (
    <>
      <Landing />
      <aside className="h-screen w-32 bg-shade-5 dark:bg-shade-9 text-shade-7 dark:text-shade-4 text-center fixed top-0 left-0 flex flex-col justify-between">
        <div className="w-full py-4 bg-shade-4 dark:bg-shade-0 flex flex-col justify-center items-center">
          <Logo className="w-4/5" />
          <span className="text-2xl font-semibold">{details.name}</span>
          <span className="text-sm opacity-75 mt-2">{details.role}</span>
        </div>

        <div className="flex justify-center items-center text-xl mb-24 text-shade-7 dark:text-shade-4 relative">
          {details.socialLinks.map((e, i) => (
            <a
              key={i}
              href={e.href}
              target="_blank"
              rel="noreferrer"
              aria-label={e.name}
              className="h-full aspect-square p-1.5 flex justify-center items-center rounded-full hover:bg-shade-7 hover:dark:bg-shade-4 hover:text-shade-5 hover:dark:text-shade-9 info"
            >
              <i className={e.iconClass}></i>
            </a>
          ))}
        </div>
      </aside>
    </>
  );
};

export default Home;
