import React, { useState, useEffect } from "react";
import { HashRouter, Routes, Route } from "react-router-dom";

import details from "./data/details.json";

import Home from "./pages/Home";

const App = () => {
  const [dark, setDark] = useState(
    localStorage.dark ? localStorage.dark === "true" : true
  );

  useEffect(() => {
    document.querySelector(
      "title"
    ).textContent = `${details.name} | ${details.role}`;
  }, []);

  useEffect(() => {
    if (dark === true) document.documentElement.classList.add("dark");
    else document.documentElement.classList.remove("dark");

    localStorage.dark = dark;
  }, [dark]);

  return (
    <HashRouter>
      <Routes>
        <Route path="/" element={<Home />} />
      </Routes>

      <button
        onClick={setDark.bind(this, !dark)}
        className="absolute top-0 right-0 m-4 px-2 text-xl rounded-full aspect-square bg-shade-7 text-shade-4 dark:bg-shade-4 dark:text-shade-0 grid place-items-center info"
        aria-label={dark ? "light" : "dark"}
      >
        <i className="far fa-moon"></i>
      </button>

      <button
        onClick={window.open.bind(this, `mailto:${details.email}`)}
        className="absolute bottom-0 right-0 m-4 px-4 text-4xl rounded-full aspect-square bg-shade-7 text-shade-4 dark:bg-shade-4 dark:text-shade-0 grid place-items-center info-reverse"
        aria-label="Say Hi!"
      >
        <i className="far fa-envelope"></i>
      </button>
    </HashRouter>
  );
};

export default App;
