import React from "react";

import details from "../data/details.json";

const Landing = () => {
  return (
    <div className="h-screen w-full pl-48 bg-shade-4 dark:bg-shade-0 text-shade-6 dark:text-shade-4 flex flex-col justify-center items-start">
      <h1 className="text-9xl font-bold w-4/5 flex flex-col gap-8">
        <p>Hi,</p>
        <p>
          I'm{" "}
          <span className="font-inspiration font-normal">{details.name}</span>,
        </p>
        <p>{details.role.toLowerCase()}</p>
      </h1>
      <h3 className="mt-4 text-4xl text-shade-4 dark:text-shade-0 bg-shade-6 dark:bg-shade-4 font-semibold px-2 tracking-widest">
        MERN Developer
      </h3>
    </div>
  );
};

export default Landing;
